<template>
    <div class="index">
        <navHead></navHead>
        <div class="container">
            <div class="row">
                <button class="btn btn-primary" @click="syncVideoList">同步视频列表</button>
            </div>
            <div class="row videoList">
                <table class="table table-bordered table-hover">
                    <thead>
                        <tr>
                            <th>视频名称</th>
                            <th width="100">视频状态</th>
                            <th width="100">分享</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in videoList">
                            <td v-if="item.disabled === 0">{{item.title}}</td>
                            <td v-if="item.disabled === 1"><del>{{item.title}}</del></td>
                            <td v-if="item.disabled === 0"><a href="javascript:void(0)" @click="setVideoStatus(item.vid)">上线</a></td>
                            <td v-if="item.disabled === 1"><a href="javascript:void(0)" @click="setVideoStatus(item.vid)">下线</a></td>
                            <td><a :href="'viewShares?vid='+item.vid" target="_blank">分享</a></td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>
<script>
    import navHead from './nav/index';
    import commonMethod from '../commonMethod';
    export default {
        data:function(){
            return {
                videoList:[],
                url:"http://chenkq.chingo.cn:8080",
            }
        },
        components:{
            navHead
        },
        methods:{
            syncVideoList:function(){
                //同步视频列表
                var _this = this;
                
                commonMethod.axiosData({
                    method:"POST",
                    url : this.url+"/video-share/php/public/api/video/syncVideoList",
                    token:true
                },function(result){
                    _this.videoList  = result.data;
                })
            },
            getVideoList:function(){
                var _this = this;
                var token = localStorage.getItem("token");

                //初始化视频列表
                commonMethod.axiosData({
                    method:"POST",
                    url : this.url+"/video-share/php/public/api/video/listVideos",
                    token:true
                },function(result){
                    _this.videoList  = result.data;
                });
            },
            setVideoStatus:function(vid){
                var _this = this;
                
                commonMethod.axiosData({
                    method:"POST",
                    url : this.url+"/video-share/php/public/api/video/disableVideo",
                    data: {
                        vid
                    }
                },function(result){
                    alert(result.message);
                    _this.getVideoList();
                })
            },
        },
        mounted:function(){
            //读取视频列表
            this.getVideoList();
        }
        
    }
</script>
<style>
    .videoList {
        padding-top:10px;
    }
    .container {
        padding-left:25px;
        padding-right:25px;
    }
    .loginFail {
        text-align:center;
        margin:100px;
        font-size:18px;
        color:#f00;
    }
    .loading {
        animation:load 1s linear infinite;
    }
    @keyframes load {
        0 {transform:rotate(0)}
        100% {transform:rotate(360deg)}
    }
</style>


