<template>
    <div class="viewShares">
        <navHead></navHead>
        <div class="container">
            <div class="row">
                <button class="btn btn-primary" @click="createNewShare" data-toggle="modal" data-target="#myModal">创建新分享</button>
            </div>
            <div class="row">
                <ul class="shareList">
                    <li class="clearfix" v-for="item in shareList">
                        <div class="col-sm-12">
                            分享<b> {{item.share_name}}</b>，提取码为<b> {{item.password}}</b>，可在{{item.dt_begin}}至{{item.dt_end}}有效观看，过期自动关闭分享，剩余观看次数{{item.watch_times_left}}/{{item.watch_times}},观看次数为0时亦不可观看。
                        </div>
                        <div class="col-sm-12">
                            由{{item.operator}}创建于{{item.dt.substring(0,10)}}<span v-if="item.last_operator">，最后一次修改由{{item.last_operator}}修改于{{item.last_dt.substring(0,10)}}</span>
                        </div>
                        <div class="col-sm-12">
                            <button class="btn btn-primary btn-sm" @click="changeShare(item.id,item.dt_begin,item.dt_end,item.share_name,item.watch_times)">修改</button>
                            <button class="btn btn-primary btn-sm" @click="copyUrl()" :data-clipboard-text="watchVideoUrl+item.password">复制地址</button>
                            <button class="btn btn-danger btn-sm" v-if="item.is_watched.length == 0" @click="delShare(item.id,item.share_name)">删除</button>
                        </div>
                        <div class="col-sm-12" v-if="item.is_watched.length > 0">
                            <b>观看记录</b>
                            <ol>
                                <li v-for = "watch_item in item.is_watched">
                                ip:{{watch_item.ip}}的用户{{watch_item.dt}}观看过本视频
                                </li>
                            </ol>
                        </div>
                        
                    </li>
                </ul>
            </div>
        </div>
        <modal 
            :show.sync = "showModal"
            :backdrop = "false"
        >
            <div class="modal-header" slot="modal-header">
                <button type="button" class="close" @click="closeModal"><span>&times;</span></button>
                <h4 class="modal-title">
                    <slot name="title">
                        创建新分享
                    </slot>
                </h4>
            </div>
            <div class="modal-body" slot="modal-body">
                <div class="form">
                    <div class="form-group">
                        <label for="share_name">分享名称</label>
                        <input type="text" class="form-control" id="share_name" :readonly = 'readOnly' v-model="shareName" placeholder="填写分享名称">
                    </div>
                    <div class="form-group">
                        <label for="dt_begin">开始时间</label>
                        <date-picker
                            :date="dtBeginOption.data" 
                            :option="dtBeginOption.option" 
                        ></date-picker>
                    </div>
                    <div class="form-group">
                        <label for="dt_end">结束时间</label>
                        <date-picker
                            :date="dtEndOption.data" 
                            :option="dtBeginOption.option" 
                        ></date-picker>
                    </div>
                    <div class="form-group">
                        <label for="watch_times">可观看次数</label>
                        <input type="number" class="form-control" v-model="watchTimes" id="watch_times" placeholder="可观看总次数">
                    </div>
                </div>
            </div>
            <div class="modal-footer" slot="modal-footer">
                <button class="btn btn-default" @click="closeModal">关闭</button>
                <button class="btn btn-primary" @click="okModal">确定</button>
            </div>
        </modal>
    </div>
</template>
<script>
    import navHead from './nav/index';
    import  commonMethod  from '../commonMethod';
    import { modal } from 'vue-strap';
    import myDatepicker from './vue-datepicker-es6.vue'
    import Clipboard from 'clipboard'
    export default {
        data:function(){
            return {
                vid:"",
                shareList:[],
                watchVideoUrl:"http://localhost:8080/watchVideo?vid=",
                url:"http://chenkq.chingo.cn:8080",
                showModal:false,
                submitType:"create",
                readOnly:false,
                dtBeginOption:{
                    data:{
                        time:""
                    },
                    option:{
                        week: ['一', '二', '三', '四', '五', '六', '七'],
                        month: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
                        type: 'min',
                        format: 'YYYY-MM-DD HH:mm',
                        placeholder: '输入开始时间',
                        buttons: {
                            ok: '确定',
                            cancel: '取消'
                        }
                    }
                },
                dtEndOption:{
                    data:{
                        time:''
                    },
                    option:{
                        week: ['一', '二', '三', '四', '五', '六', '七'],
                        month: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
                        type: 'min',
                        format: 'YYYY-MM-DD HH:mm',
                        placeholder: '输入结束时间',
                        buttons: {
                            ok: '确定',
                            cancel: '取消'
                        }
                    }
                },
                shareName:"",
                watchTimes:"",
            }
        },
        components:{
            navHead,
            modal,
            "date-picker": myDatepicker
        },
        methods:{

            getVid(){
                var urlhash = window.location.search?window.location.search:window.location.hash;
                var vid = urlhash.search("vid=") !=-1 ? urlhash.substring(urlhash.search("vid=")+4) : null;
                this.vid = vid;
            },
            getShareList(){
                var _this = this;
                commonMethod.axiosData({
                    method:"POST",
                    url:this.url+"/video-share/php/public/api/video/viewShares",
                    data:{
                        vid:this.vid
                    },
                    token:true
                },function(result){
                    _this.shareList = result.data;
                })
            },
            createNewShare(){
                this.showModal = true;
                this.submitType = "create";
                this.readOnly = false;
                this.shareName = "";
                this.watchTimes = "";
                this.dtBeginOption.data.time = "";
                this.dtEndOption.data.time = "";
            },
            changeShare(id,dt_begin,dt_end,share_name,watch_times){
                this.showModal = true;
                this.submitType = "change";
                this.readOnly = true;
                this.shareId = id;
                this.shareName = share_name;
                this.watchTimes = watch_times;
                this.dtBeginOption.data.time = dt_begin;
                this.dtEndOption.data.time = dt_end;
            },
            closeModal(){
                this.showModal = false;
            },
            okModal(){
                var _this = this;
                var data = {
                    vid:this.vid,
                    share_name:this.shareName,
                    dt_end:this.dtEndOption.data.time,
                    dt_begin:this.dtBeginOption.data.time,
                    watch_times:this.watchTimes
                }
                if(this.submitType === "create"){
                    //创建新分享
                    commonMethod.axiosData({
                        method:"POST",
                        url:this.url+"/video-share/php/public/api/video/createShare",
                        data:data,
                        token:true
                    },function(result){
                        _this.showModal = false;
                        _this.getShareList();
                    })
                }else if(this.submitType === "change"){
                    //修改分享
                    data.v_share_id = this.shareId;
                    
                    commonMethod.axiosData({
                        method:"POST",
                        url:this.url+"/video-share/php/public/api/video/editShare",
                        data:data,
                        token:true
                    },function(result){
                        _this.showModal = false;
                        _this.getShareList();
                    })
                }
            },
            delShare(id,share_name){
                var _this = this;
                if(confirm("删除操作是不可逆的，请仔细确认，是否删除 <"+share_name+"> 这条分享")){
                    commonMethod.axiosData({
                        method:"POST",
                        url:this.url+"/video-share/php/public/api/video/deleteShare",
                        data:{
                            id
                        },
                        token:true
                    },function(result){
                        _this.getShareList();
                    })
                }
            },
            copyUrl(){ 
                var clipboard = new Clipboard(".btn");
                clipboard.on("success",function(e){
                    alert("复制成功，地址为："+e.text);
                })
            }
        },
        mounted:function(){
            this.getVid();
            this.getShareList();
        }
    }
</script>
<style>
    .container {
        padding-left:25px;
        padding-right:25px;
    }
    .shareList {
        padding-top:10px;
    }
    ul {
        padding:0;
    }
    li {
        list-style:none;
    }
    .shareList>li {
        border-right:1px solid #ccc;
        border-top:1px solid #ccc;
        margin-bottom:15px;
        background-color:#f9f9f9;
        line-height:24px;
    }
    .shareList li>div {
        border-left:1px solid #ccc;
        border-bottom:1px solid #ccc;
        padding:5px 15px;
    }
    .datepicker {
        display:block;
    }
</style>


