import md5 from "js-md5";
import axios from 'axios';
import qs from 'qs';
//发起网络请求
function axiosData(opt, success, fail) {
    //设置请求头
    var token = getCookie("token");
    const TOKEN_ERROR = "1013500"; //token错误
    const TOKEN_OLD = "1013501";   //token过期

    setCookie("srouce", window.location.href, 1);
    //要传token 但是 没有token的情况就去 登录
    if (!token && opt.token) {
        verificationLogin();
    }
    // 不要传token的情况 或者 token有的情况
    if (opt.token) {
        opt.headers = getAuthHeader();
        delete opt.token;
    }
    axios(opt)
        .then(function (response) {
            return response.data;
        })
        .then(function (data) {
            if (data.code === "200") {
                success && success(data);
            } else if (data.code === TOKEN_ERROR || data.code === TOKEN_OLD) {
                //token过期或者错误  清除token 然后在执行一遍
                clearCookie("token");
                verificationLogin();
            } else {
                console.log(data);
                alert(data.message);
                fail && fail();
            }
        })
        .catch(function (error) {
            console.log(error);
        });

}
function getCookie(c_name) {
    if (document.cookie.length > 0) {
        var c_start = document.cookie.indexOf(c_name + "=")
        if (c_start != -1) {
            c_start = c_start + c_name.length + 1
            var c_end = document.cookie.indexOf(";", c_start)
            if (c_end == -1) c_end = document.cookie.length
            return unescape(document.cookie.substring(c_start, c_end))
        }
    }
    return ""
}
function setCookie(cname, cvalue, exdays) {
    var d = new Date();
    d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
    var expires = "expires=" + d.toUTCString();
    document.cookie = cname + "=" + cvalue + "; " + expires + ";path=/;domain=chingo.cn";
}
//清除cookie    
function clearCookie(name) {
    setCookie(name, "", -1);
}
//验证登录
function verificationLogin() {
    window.location.href = "https://cas.crigh.net/cas/login?service=http%3A%2F%2Fchenkq.chingo.cn%3A8080%2Fvideo-share%2Fphp%2Fpublic%2Fapi%2Fvideo%2Flogin";
}

//带有token的请求头
function getAuthHeader() {
    return {
        'Authorization': getCookie("token")
    }
}
export default {
    axiosData,
    verificationLogin,
    getAuthHeader
}


