import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import viewShares from '@/components/viewShares'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/viewShares',
      name: 'viewShares',
      component: viewShares
    }
  ]
})



