<template>
    <div class="siteHeader">
        <div id="header"></div>
        <div class="chingo_nav">
            <ul>
                <li v-for="item in navData"><a :href="item.url">{{item.title}}</a></li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name:"nav",
    data:function(){
        return {
            navData:[{
                url:"/",
                title:"视频列表",
            }]
        }
    }
}
</script>


